<template>
  <div>
    <Header title="anime quote"></Header>
    <Quote :quote="quote"></Quote>
    <button @click="newQuote" :disabled="this.current == this.max">
      New quote
    </button>
    <quoteList :quotesl="quotes.slice(0, current)"></quoteList>
  </div>
</template>

<script>
import Header from "./components/Header.vue";
import Quote from "./components/Quote.vue";
import quoteList from "./components/quotesList.vue";

export default {
  name: "App",
  components: { Header, Quote, quoteList },
  data() {
    return {
      current: 0,
      max: 10,
      quote: {},
      quotes: [{ conent: "waiting", anime: "", author: "yojanes" }],
    };
  },
  methods: {
    newQuote() {
      this.current++;
      if (this.current == this.max) {
        this.quote = {
          content: "only ten result ",
          anime: "none",
          author: "none",
        };
      } else {
        this.quote = {
          content: this.quotes[this.current].quote,
          anime: this.quotes[this.current].anime,
          author: this.quotes[this.current].character,
        };
      }
    },
    async getQuotes() {
      console.log("pasa")
      this.quotes = await fetch("https://animechan.vercel.app/api/quotes")
        .then((response) => response.json())
        .then((quotes) => (this.quortes=quotes));
    },
  },
  mounted() {
    this.getQuotes()
    this.quote = {
      content: this.quotes[0].quote,
      anime: this.quotes[0].anime,
      author: this.quotes[0].character,
    };
  },
};
</script>

