<template>

<div class="quote">
    <div class="quote-content">
        {{quote.content}}
        <div class="quote-anime">
           {{quote.anime}}
        </div>
        <div class="quote-author">
            {{quote.author}}
        </div>
    </div>
</div>
  
</template>

<script>
export default {
    props:["quote"]

}
</script>

<style>
.quote{
    position:relative;
    margin:0 auto;
    padding: 10px 32px;
    max-width:720px;
}

.quote .quote-content{
    position:relative;
    font-size:1.5em;
    font-weight: bold;
    background-color:var(--dark);
    color:var(--light);
    padding:48px 32px;
    border-radius:16px;

}
 .quote .quote-content::before,.quote .quote-content::after{
     content:'"';
     color:var(--light);
     margin:0 2px

 }
.quote-anime, .quote-author{
    position:absolute;
    padding:12px 16px;
    font-size:12px;
    font-weight: bold;
    border-radius: 99px;
    color: var(--light);
    text-align: center;


}


.quote-author{
    bottom: 0;
    right:0;
    transform:translateY(50%) translateX(16px);
    background-color: var(--secondary);

}

.quote-anime{
    top: 0;
    left: 0;
    background-color: var(--tertiary);
    color: var(--dark);
    transform:translateY(-50%) translateX(-16px)
}

</style>